@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(55deg , #161515dd 100% , rgba(22, 20, 20, 0.829)  90%) ;
  
 
 
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
a{
  text-decoration: none;
}

.App {
  text-align: center;
}

a:hover{
  
  opacity: 0.8;
}
ul{
  list-style: none;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.box{
  width:90vw;
  height:80vh;
  margin:5% auto 0px auto;
  background:#18181894;
  overflow: hidden;

  box-shadow:5px 3px 7px 4px #7e777727;
}
.box > *{
  position: static;
}

.container-navbar{
background-color: #18161542;
 margin-top: 10px;
 padding-top: 10px;
}
.navbar{
  margin: auto 10%;
}
ul{
  margin-top: 10px;
  /* background: #61dafb; */
  display: flex;
  margin: 0px;
  padding: 0px;
}
li{
  float: left;
  flex-grow: 2;
  align-self: start;
  display: inline; 
  text-align: right;
  margin: auto 0px;
} 
.title{
  flex-grow: 1;
  margin-left: -10%;
  text-align: left;
  /* align-self: flex-start; */
  
  

}
.logo{
  text-shadow: 2px 2px 10px rgb(211, 144, 144);
  font-weight:bolder;
  font-size: 23px;
  color:red
}
.container{
  width: 100%;
  height: 100%;
  
  /* background-color: rgb(120, 141, 123); */
}
.imgd{
 /* background: #61dafb; */
 margin-top: -3%;
  float: right;
  width:50%;
  max-height: 70%;
}
#img{
  /* background: #61da23; */
  margin: 20px auto;
  min-width: 100%;
  max-width:90%;
  max-height: 80%;

}
.about{
  text-align: center;
  width: 40%;
  height: 60%;
  /* height: 100vh; */
  margin: auto 0px;
    /* background-color: beige; */
  float: left;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-me{
  font-weight: bolder;
  line-height: 1.5em;
  font-size: 27px;
  color: white;
  text-align: left;
  font-family:'Anton ', monospace;
  /* background-color: blue; */
}
#yellow{
  color:#e64862;
}
#skills{
  font-size: 17px;
  color:rgb(97, 93, 93);
  font-style: italic;
}
.contact{
  /* background-color: indianred; */
  margin:auto 30px;
  color:red;
  
}
button{
  border: none;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  background-color:#18181825 ;
  border-radius: 10%;
  color:#e693f1;
  height: 4vh;
  font-size: 18px;
  font-weight: bolder;
  font-family: sans-serif;
  /* border: 1px solid #e693f1; */
  box-shadow: 0 4px 4px 0 rgba(219, 179, 231, 0.2), 0 6px 5px 0 rgba(219, 146, 146, 0.19);

}
.hide{
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(12, 9, 9, 0.63);
}
#center{

  /* background-color: salmon; */
  
  margin: 50vh 20%;
}
@media (max-width: 670px){
  .box{
    width:100vw;
    height:100vh;
    margin:0px;
  }


  .container{
  display: flex;
  flex-direction: column;
  }
  .imgd{
    order: 1;
    width: 100%;
    max-height: 40vh;
    /* background: indigo; */
     float: center; 
    margin: 10px auto;
  }
  #img{
    padding-bottom: 30px;
    margin-right: 10px;
    background-color: rgba(199, 104, 207, 0.137);
    border-radius: 50%;
    margin: 0px;
    min-width: 80%;
  max-width:110%;
  max-height: 100%;
  }
  .about{
    order: 2;
    margin-top: 10%;
    /* margin: auto 20px; */
    float: none;
    display: block;
    width: 100%;
    /* background: lavenderblush;  */
  }
  .about-me{
    text-align: center;
  }

}
